<h1 id="ttl-indexes" tabindex="-1"><a class="header-anchor" href="#ttl-indexes" aria-hidden="true">#</a> TTL indexes</h1>
<p>https://blog.csdn.net/jianlong727/article/details/54631124</p>
<p>TTL indexes 是特殊的单一属性索引。</p>
<p>automatically remove documents from a collection after a certain amount of time or at a specific clock time。</p>
<p>可以自动的从集合中移除文档，在一定数量时间之后或者在一个特定的时间点。</p>
<h2 id="实现原理-mongo是如何实现自动删除的" tabindex="-1"><a class="header-anchor" href="#实现原理-mongo是如何实现自动删除的" aria-hidden="true">#</a> 实现原理，mongo是如何实现自动删除的</h2>
<p>据说是后台有个单线程，一直查询</p>
<h2 id="创建ttl索引" tabindex="-1"><a class="header-anchor" href="#创建ttl索引" aria-hidden="true">#</a> 创建TTL索引</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>db.autoCrawlResult.createIndex<span class="token punctuation">(</span> <span class="token punctuation">{</span> <span class="token string">"createdTime"</span><span class="token builtin class-name">:</span> <span class="token number">1</span> <span class="token punctuation">}</span>, <span class="token punctuation">{</span> expireAfterSeconds: <span class="token number">3600</span> <span class="token punctuation">}</span> <span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>